.list {
    color: #fff;
    width: 300px;
    background-color: royalblue;
    margin: 20px auto;
    border-radius: 5px;
}

.list p {
    padding: 10px;
    position: relative;
}

.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.list p span :hover{
    font-size: 20px;
}


.faicons{
    margin: 0 5px;
    font-size: 15px;
}


.list p input{
    background-color: transparent;
    border: 0;
    color: #fff;
    font-size: 18px;
}

.list p input :focus{
    outline: none;
}